import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ViewController, AlertController } from 'ionic-angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
/**
 * Generated class for the SelectCommunityNamePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-community-name',
  templateUrl: 'select-community-name.html',
})
export class SelectCommunityNamePage {
  form;
  communitylist = [
  ];

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectCommunityNamePage');
  }

  constructor(public viewCtrl: ViewController, private alertCtrl: AlertController) {
    this.form = new FormGroup({
      communityName: new FormControl("", Validators.required),
    });

  }

  dismiss(param) {
    this.viewCtrl.dismiss(param ? param : '');
  }

  processForm() {
    console.log(this.form.value.communityName);
    if (this.form.status === 'VALID') {
      this.dismiss(this.form.value.communityName);
    } else {
      this.presentAlert('输入不合法', '请输入小区名称');
    }
  }

  presentAlert(title: string, subTitle: string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['确定']
    });
    alert.present();
  }
}



